import React from 'react'
import interact from 'interactjs'
import '../App.css'

class RandomScreen extends React.Component {
  render() {
    const { randomTune, setNewRandomTune } = this.props

    if (randomTune) return (
      <div className="Random-screen">
        <canvas className="Random-tune-container">
          <div className="Random-tune">
            <span className="Tune-title">
              {randomTune.title}
            </span>
            <span className="Tune-detail">
              {randomTune.composer} ({randomTune.year})
            </span>
          </div>
        </canvas>
        <div className="Next-button" onClick={setNewRandomTune}>
          NEXT
        </div>
      </div>
    )
    else return (
      <div className="Random-screen">
        <div className="Random-tune">
          <span className="Tune-detail">Loading random tune...</span>
        </div>
      </div>
    )
  }
  //onClick={setNewRandomTune}

  componentDidMount() {
    interact('.Random-tune')
      .draggable({
        inertia: true,
        onmove: this.onMove,
        restrict: {
          restriction: "parent"
        }
      })
      // .on('dragmove', (e) => {
      //   const dragAngle = 180 * Math.atan2(e.dx, e.dy) / Math.PI

      //   console.log(dragAngle)
      // })
  }

  onMove(e) {
    console.log(e)
    // const target = e.target
    // const x = (parseFloat(target.getAttribute('data-x')) || 0) + e.dx
    // const y = (parseFloat(target.getAttribute('data-y')) || 0) + e.dy

    // // translate the element
    // target.style.webkitTransform =
    // target.style.transform =
    //   'translate(' + x + 'px, ' + y + 'px)'

    // // update the posiion attributes
    // target.setAttribute('data-x', x)
    // target.setAttribute('data-y', y)
  }
}

export default RandomScreen


