import React from 'react'
import {
  BrowserRouter as Router,
  Route } from 'react-router-dom'
import {
  TunesScreen,
  SetlistScreen,
  RandomScreen } from './containers'
import { 
  Header,
  MenuDrawer } from './components'
import {
  debounce,
  findIndex } from 'lodash'
import Hammer from 'hammerjs'
import { TweenLite } from 'gsap'
import tunesJson from './tunes'
import './App.css'

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      activeRoute: '/tunes',
      menuIsOpen: false,
      tunesScrollPosition: 0,
      setlistScrollPosition: 0,
      tunes: tunesJson,
      filteredTunes: tunesJson,
      filter: 'all',
      sortBy: 'title',
      setlist: [],
      randomTune: {}
    }
  }

  componentDidMount() {
    // Get tunes from API
    // Uncomment in production
    // ----------------------------------------------------
    // fetch('https://thomas-mcevoy.com/api/tunes/')
    //   .then(response => JSON.parse(response))
    //   .then(responseJson => this.setState({
    //     tunes: responseJson,
    //     filteredTunes: responseJson
    //   }))
    //   .catch(error => console.log(error))

    this.setNewRandomTune()

    this.hammer = new Hammer(this.refs.app, {touchAction: 'auto'})
    this.hammer.get('swipe').set({ direction: Hammer.DIRECTION_VERTICAL })
    this.hammer.on('swipeup', (e) => this.menuSwipeClosed(e))

    window.addEventListener('scroll', debounce((e) => {
      if (window.location.pathname === '/tunes')
        this.setState({tunesScrollPosition: window.scrollY})
      else if (window.location.pathname === '/setlist')
        this.setState({setlistScrollPosition: window.scrollY})
    }, 100))
  }

  render() {
    return (
      <Router>
        <div className="App" ref="app">
          <Header
            toggleMenu={this.toggleMenu}
            menuIsOpen={this.state.menuIsOpen}
          />
          <MenuDrawer
            ref="menuDrawer"
            menuIsOpen={this.state.menuIsOpen}
            menuSwipeClosed={this.menuSwipeClosed}
            sortBy={this.state.sortBy}
            setSortBy={this.setSortBy}
            filter={this.state.filter}
            setFilter={this.setFilter}
          />
          <main className="App-main">
            <Route path="/tunes" render={() =>
              <TunesScreen
                tunes={this.state.filteredTunes}
                sortBy={this.state.sortBy}
                toggleSelected={this.toggleSelected}
                scrollPosition={this.state.tunesScrollPosition}
                setScrollPosition={this.setTunesScrollPosition}
              />
            }/>
            <Route path="/setlist" render={() =>
              <SetlistScreen
                setlist={this.state.setlist}
                toggleSelected={this.toggleSelected}
                scrollPosition={this.state.setlistScrollPosition}
              />
            }/>
            <Route path="/random" render={() => 
              <RandomScreen
                randomTune={this.state.randomTune}
                setNewRandomTune={this.setNewRandomTune}
                toggleSelected={this.toggleSelected}
              />
            }/>
          </main>
        </div>
      </Router>
    )
  }
  
  getNewRandomTune = () => {
    let randomTune
    const { filteredTunes, setlist } = this.state
    
    do {
      const index = Math.floor(Math.random() * filteredTunes.length)
      randomTune = filteredTunes[index]
    }
    while (findIndex(setlist, {title: randomTune.title}) > -1)
    
    return randomTune
  }

  setNewRandomTune = () => {
    this.setState({ randomTune: this.getNewRandomTune() })
  }

  getFilteredTunes = () => {
    const { tunes, filter } = this.state

    return tunes.filter(tune => {
      switch(filter) {
        case '':
          return false
        case 'all':
          return true
        case 'blues':
          if (tune.form && tune.form === 'blues') return true
          break
        case 'rhythm':
          if (tune.form && tune.form === 'rhythm') return true
          break
        case 'latin':
          if (tune.latin) return true
          break
        case 'waltz':
          if (tune.waltz) return true
          break
        case 'holiday':
          if (tune.type && tune.type === 'holiday') return true
          break
        case 'miles':
          if (tune.artist && tune.artist === 'miles') return true
          break
        case 'sinatra':
          if (tune.artist && tune.artist === 'sinatra') return true
          break
        case 'standards':
          const STANDARDS_COMPOSERS = [
            'Jerome Kern', 'Irving Berlin', 'George Gershwin',
            'Vincent Youmans', 'Richard Rodgers', 'Jimmy McHugh',
            'Hoagy Carmichael', 'Harold Arlen', 'Cole Porter',
            'Sigmund Romberg', 'Kurt Weill', 'Arthur Schwartz',
            'Irving Caesar', 'Richard A. Whiting', 'Johnny Green',
            'Harry Warren', 'Victor Young', 'Harry Barris', 
            'Bernice Petkere', 'Vernon Duke', 'Burton Lane',
            'Ben Oakland', 'Ray Noble', 'J. Fred Coots', 'Matt Malneck',
            'Ralph Rainger', 'Johnny Mercer', 'Arthur Johnston',
            'Allie Wrubel', 'Bronislaw Kaper', 'Jimmy Van Heusen',
            'Sammy Fain', 'Irene Kitchings', 'Matt Dennis', 
            'Gene De Paul', 'Jule Styne', 'Alec Wilder', 'Frank Loesser',
            'Leonard Bernstein', 'Rube Bloom', 'Josef Myrow', 
            'Frederick Loewe', 'Carl Sigman', 'Johnny Mandel', 
            'Henry Mancini' ]
          for (let i = 0; i < STANDARDS_COMPOSERS.length; i++) {
            if (tune.composer.indexOf(STANDARDS_COMPOSERS[i]) > -1) {
              return true
            }
          }
          break
        case 'jazz-tunes':
          const JAZZ_COMPOSERS = [
            'Jelly Roll Morton', 'James P. Johnson', 'Fats Waller', 
            'Eubie Blake', 'Duke Ellington', 'Billy Strayhorn', 
            'Tadd Dameron', 'Billie Holiday', 'Benny Goodman',
            'Count Basie', 'Neal Hefti', 'Thelonious Monk', 'Kenny Clarke', 
            'Charlie Parker', 'Dizzy Gillespie', 'Mary Lou Williams', 
            'John Louis',  'J. J. Johnson', 'Bud Powell', 'Oscar Pettiford',
            'Denzil Best', 'George Shearing', 'Gigi Gryce', 'Duke Jordan',
            'Miles Davis', 'Sonny Rollins', 'Milt Jackson', 'Horace Silver',
            'Dave Brubeck', 'Jackie McLean', 'Bill Evans', 'Benny Golson',
            'John Coltrane', 'Tommy Flanagan', 'Mal Waldron', 'Jimmy Heath',
            'Bobby Timmons', 'Wes Montgomery', 'Oliver Nelson', 'Lee Morgan',
            'Cannonball Adderley', 'Frank Rosolino', 'Duke Pearson',
            'Rudy Stevenson', 'Barry Harris', 'Freddie Hubbard', 
            'Kenny Dorham', 'Joe Henderson', 'Herbie Hancock', 'Chick Corea',
            'Victor Feldman', 'Wayne Shorter', 'Bobby Hutcherson', 
            'Frank Foster', 'Thad Jones', 'Hank Jones', 'Cedar Walton', 
            'Jimmy Rowles', 'Walter Davis Jr.', 'Wynton Marsalis' ]
          for (let j = 0; j < JAZZ_COMPOSERS.length; j++) {
            if (tune.composer.indexOf(JAZZ_COMPOSERS[j]) > -1) {
              return true
            }
          }
          break
        default:
          break
      }
      return false
    })
  }

  toggleMenu = () => {
    this.setState((prevState, _) => {
      return { menuIsOpen: !prevState.menuIsOpen }
    })
  }

  setSortBy = (sortBy) => {
    this.setState((prevState, _) => {
      return {
        sortBy: sortBy,
        tunes: [...prevState.tunes].sort((a, b) => {
          if (sortBy === 'year') 
            return a.year - b.year
          else {
            if (a[sortBy] < b[sortBy]) return -1
            if (a[sortBy] > b[sortBy]) return 1
            return 0
          }
        })
      }
    }, () => this.setState({ filteredTunes: this.getFilteredTunes() }))
  }

  setFilter = (str) => {
    this.setState({ filter: str }, () => {
      this.setState({ filteredTunes: this.getFilteredTunes() })
    })
  }

  setTunesScrollPosition = (offset) => {
    const headerHeight = document.getElementsByTagName('header')[0].clientHeight
    
    this.setState({ tunesScrollPosition: offset }, () => {
      window.scrollTo(0, offset - headerHeight)
    })
  }
  
  menuSwipeClosed = (e) => {
    if (this.state.menuIsOpen) {
      TweenLite.to(this.refs.menuDrawer, .1, {y: 0})
      this.setState({ menuIsOpen: false })
    }
  }

  toggleSelected = (tune) => {
    this.setState((prevState, _) => {
      const { tunes, setlist } = prevState

      let nextTunes = [...tunes]
      const tunesIndex = findIndex(tunes, {title: tune.title})
      let updatedTune = Object.assign({}, tunes[tunesIndex])
      updatedTune.selected = updatedTune.hasOwnProperty('selected') ? 
        !updatedTune.selected : true
      nextTunes[tunesIndex] = updatedTune
      
      let nextSetlist = [...setlist]
      const setlistIndex = findIndex(setlist, {title: tune.title})
      if (setlistIndex === -1) nextSetlist.push(tune)
      else nextSetlist.splice(setlistIndex, 1)
      
      return {
        tunes: nextTunes,
        setlist: nextSetlist
      }
    }, () => this.setState({
      filteredTunes: this.getFilteredTunes(this.state.tunes)
    }))
  }
}

export default App



