import React from 'react'
import Hammer from 'hammerjs'
import { TimelineLite } from 'gsap'
import '../App.css'

class SetlistTune extends React.Component {
  componentDidMount() {
    this.node = this.refs.setlistTune
    this.hammer = Hammer(this.node)
    this.hammer.on('panmove', (e) => this.handlePanMove(e))
    this.hammer.on('panend', (e) => this.handlePanEnd(e))
  }

  componentWillUnmount() {
    this.hammer.off('panmove', (e) => this.handlePanMove(e))
    this.hammer.off('panend', (e) => this.handlePanEnd(e))
  }

  handlePanMove(e) {
    const getXFontSize = dx => dx < 125 ? (dx / 64).toString() + 'em' : '1.5em'
    
    if (e.deltaX <= 0) {
      this.node.style.transform = `translateX(${e.deltaX}px)`
      this.refs.x.style.fontSize = getXFontSize(Math.abs(e.deltaX))
    }
  }

  handlePanEnd(e) {
    if (e.deltaX > -125)
      this.node.style.transform = 'translateX(0)'
    else {
      const tl = new TimelineLite({
        onComplete: () => this.props.toggleSelected(this.props.tune)
      })
      tl.to(this.node, .1, { x: -window.innerWidth })
      tl.to(this.node, .3, { height: 0 })
    }
  }

  render() {
    const { tune } = this.props
    
    return (
      <li className="Setlist-tune-container">
        <div className="Setlist-tune" ref="setlistTune">
          <div className="Tune-title">{tune.title}</div>
          <div className="Tune-detail">{tune.composer} ({tune.year})</div>
        </div>
        <div className="x-container">
          <div className="x" ref="x">×</div>
        </div>
      </li>
    )
  }
}

export default SetlistTune


