import React from 'react'
import { SeekBar, Tune } from '../components'
import '../App.css'

class TunesScreen extends React.Component {
  componentDidMount() {
    window.scrollTo(0, this.props.scrollPosition)
  }

  render() {
    const { sortBy, setScrollPosition } = this.props

    return (
      <div className="Tunes-screen">
        <SeekBar sortBy={sortBy} setScrollPosition={setScrollPosition}/>
        <ul className="Tunes-list">
          {sortBy === 'year' ? this.getTunesByYear() : this.getTunesByChar()}
        </ul>
      </div>
    )
  }

  getTunesByYear() {
    let nextId = '<'

    const generateTune = (tune, id) => {
      if (id) {
        nextId = id === '<'
        ? '10' 
        : String.fromCharCode(nextId.charCodeAt(0) + 1) + '0'
      }
      
      return <Tune id={id} tune={tune} key={tune.title} 
        toggleSelected={this.props.toggleSelected}/>
    }

    return this.props.tunes.map(tune => {
      if (nextId === '<' || tune.year >= parseInt('19' + nextId, 10))
        return generateTune(tune, nextId)
      else
        return generateTune(tune)
    })
  }

  getTunesByChar() {
    let nextId = 'A'

    const generateTune = (tune, id) => {
      if (id) nextId = String.fromCharCode(nextId.charCodeAt(0) + 1)
      return <Tune id={id} tune={tune} key={tune.title} 
        toggleSelected={this.props.toggleSelected}/>
    }

    return this.props.tunes.map(tune =>
      nextId === tune[this.props.sortBy].charAt(0) 
      ? generateTune(tune, nextId) 
      : generateTune(tune))
  }
}

export default TunesScreen


