import React from 'react'
import classNames from 'classnames'
import '../App.css'

class SubmenuItem extends React.Component {
  render() {
    const labelLowerCase = this.props.label.toLowerCase()
    
    return (
      <div
        onClick={() => this.onClick(labelLowerCase)}
        className={this.getClassNames(labelLowerCase)}
      >
        { this.getLabel(labelLowerCase) }
      </div>
    )
  }

  onClick(label) {
    if (this.props.sortBy)
      this.props.setSortBy(label)
    else
      this.props.setFilter(label)
  }

  getClassNames(label) {
    const current = this.props.sortBy ? this.props.sortBy : this.props.filter
    return classNames({
      'Submenu-item': true,
      'active': current === label
    })
  }

  getLabel(label) {
    if (label === 'jazz-tunes') return 'Jazz compositions'
    if (label === 'rhythm') return 'Rhythm changes'
    if (label === 'holiday') return 'Holiday songs'
    if (label === 'waltz') return 'Waltzes'
    if (label === 'miles') return 'Miles Davis'
    return this.props.label
  }
}

export default SubmenuItem


