import React from 'react'
import Hammer from 'hammerjs'
import classNames from 'classnames'
import { SubmenuItem } from '.'
import '../App.css'

class MenuDrawer extends React.Component {
  componentDidMount() {
    this.hammer = Hammer(this.refs.menuDrawer)
    this.hammer.get('swipe').set({ direction: Hammer.DIRECTION_HORIZONTAL })
    this.hammer.on('swiperight', (e) => this.props.menuSwipeClosed(e))
  }

  render() {
    const { menuIsOpen, menuSwipeClosed, 
            sortBy, setSortBy, filter, setFilter } = this.props

    return (
      <div ref="menuDrawer" className={classNames({
        'Menu-drawer': true,
        'hidden': !menuIsOpen
      })}>
        <div className="Menu-close-button" onClick={menuSwipeClosed}>×</div>
        <div className="Submenu">
          <div className="Submenu-title Sort-by">sort by</div>
          <div className="Submenu-item-container">
            <SubmenuItem sortBy={sortBy} setSortBy={setSortBy} label='Title'/>
            <SubmenuItem sortBy={sortBy} setSortBy={setSortBy} label='Composer'/>
            <SubmenuItem sortBy={sortBy} setSortBy={setSortBy} label='Year'/>
          </div>
        </div>
        <div className="Submenu">
          <div className="Submenu-title Filter">filter</div>
          <div className="Submenu-item-container">
            <SubmenuItem filter={filter} setFilter={setFilter} label='All'/>
            <SubmenuItem filter={filter} setFilter={setFilter} label='Jazz-tunes'/>
            <SubmenuItem filter={filter} setFilter={setFilter} label='Standards'/>
            <SubmenuItem filter={filter} setFilter={setFilter} label='Blues'/>
            <SubmenuItem filter={filter} setFilter={setFilter} label='Rhythm'/>
            <SubmenuItem filter={filter} setFilter={setFilter} label='Latin'/>
            <SubmenuItem filter={filter} setFilter={setFilter} label='Waltz'/>
            <SubmenuItem filter={filter} setFilter={setFilter} label='Holiday'/>
            <SubmenuItem filter={filter} setFilter={setFilter} label='Miles'/>
            <SubmenuItem filter={filter} setFilter={setFilter} label='Sinatra'/>
          </div>
        </div>
      </div>
    )
  }
}

export default MenuDrawer


